
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 20px;
  padding: 0rem 0rem;
    /*background-image: url(assets/watersky.jpg);*/
    background-repeat: no-repeat;
    background-size: cover;
}

.word-search[data-v-128d37a8] {
  font-size: 1.2rem;
  white-space: nowrap;
  display: inline-block;
  width: 70%;
  float: left;
}
.word-list-container[data-v-128d37a8] {
  display: inline-block;
  width: 25%;
  background: #e8e8e8;
  padding: 0.5rem;
}
.results-container[data-v-128d37a8] {
  clear: both;
}
input[type="text"][data-v-128d37a8]{
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #333;
  width: 300px;
  font-size: 1.2rem;
  color: #2c3e50;
  font-weight: 300;
  background: rgba(0,0,0,0.02);
  padding: 0.5rem;
}
button[data-v-128d37a8]{
  background: #333;
  padding: 0.5rem;
  font-weight: 300;
  color: #fff;
  border: none;
  cursor: pointer;
  font-size: 1.4rem;
  border-radius: 0;
}
button.add-word[data-v-128d37a8] {
  background: #e8e8e8;
  color: #333;
  font-size: 0.8rem;
}
button.add-word[data-v-128d37a8]:hover {
  background: #fde300;
}
button.remove-word[data-v-128d37a8] {
  font-size: 0.5rem;
  padding: 2px;
  display: inline-block;
  color: #333;
  background: none;
}
button.remove-word[data-v-128d37a8]:hover {
  background: #aa0000;
  color: #fde300;
}
h1[data-v-128d37a8], h2[data-v-128d37a8] {
  font-weight: normal;
}
ul.results[data-v-128d37a8], ul.word-list[data-v-128d37a8] {
  list-style-type: none;
  padding: 0;
}
.word-list li[data-v-128d37a8] {
  margin: 5px 0;
  padding: 5px 0;
  border-bottom: 1px solid #333;
}
.results li[data-v-128d37a8] {
  display: inline-block;
  margin: 10px;
  border: solid 1px #333;
  padding: 0.5rem;
  width: 200px;
  min-height: 100px;
  color: #fff;
  font-weight: 300;
  font-size: 1.2rem;
  background: rgba(0,0,0,0.7);
}
ul.errors[data-v-128d37a8] {
  list-style-type: none;
}
.errors li[data-v-128d37a8] {
  border: 1px solid red;
  color: red;
  padding: 0.5rem;
  margin: 10px 0;
}
a[data-v-128d37a8] {
  color: #42b983;
}
